.sidebar {
    color: red;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 82px;
    border-bottom: 1px solid var(--color-grey);
    height: var(--top-menu-height);

    @include tablet {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        width: var(--sidebar-width);
        height: var(--viewport-height);
        border-left: 1px solid var(--color-grey);
    }
}
