body {
    --grid-columns: 4;
    --container-gap: 35px;
    --container-width: 100vw;
    --offset: 10px;
    --media-height: 233px;
    --page-header-logo-size: 105px;
    --scrollbar: 0px; // stylelint-disable-line length-zero-no-unit
    --viewport-height: 100vh;

    --sidebar-width: 82px;
    --top-menu-height: 65px;


    /* colors */
    --color-black: #{$color-black};
    --color-white: #{$color-white};
    --color-main-dark: #{$color-main-dark};
    --color-beige: #{$color-main-dark};
    --color-red: #{$color-red};
    --color-grey: #{$color-grey};

    @include mobile-tiny-only {
        --page-header-logo-size: 70px;
    }

    @include tablet {
        --grid-columns: 8;
        --ofset: 13px;
        --media-height: 350px;
    }

    @include desktop {
        --container-width: calc(100vw - var(--scrollbar));
        --grid-columns: 12;
        --offset: 16px;
        --container-gap: 80px;
        --page-header-logo-size: #{col(1)};
    }

    @include desktop-wide {
        --container-width: 1838px;
        --container-gap: 80px;
    }
}
