/*  reset default styles */

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

.title-large {
    font-family: $ff-financier-display;
    font-weight: $fw-regular;
    font-size: 45px;
    margin-top: 20px;

    @include desktop {
        font-size: 120px;
        margin-top: 40px;
    }
}

ul {
    @include reset-list;
}

img {
    vertical-align: top;
}
