
/* prettier-ignore */
$fonts: (
    ('name': $financier-display-ff,  'style': normal, 'weight': $fw-light, 'file': 'FinancierDisplay-Light'),
    ('name': $financier-display-ff,  'style': italic, 'weight': $fw-light, 'file': 'FinancierDisplay-LightItalic'),
    ('name': $financier-text-ff,  'style': normal, 'weight': $fw-bold, 'file': 'FinancierText-Bold'),
    ('name': $financier-text-ff,  'style': italic, 'weight': $fw-bold, 'file': 'FinancierText-BoldItalic'),
    ('name': $financier-text-ff,  'style': normal, 'weight': $fw-regular, 'file': 'FinancierText-Regular'),
    ('name': $financier-text-ff,  'style': italic, 'weight': $fw-regular, 'file': 'FinancierText-RegularItalic'),
    ('name': $helvetica-neue-ff,  'style': normal, 'weight': $fw-bold, 'file': 'HelveticaNeue-Bold'),
    ('name': $helvetica-neue-ff,  'style': normal, 'weight': $fw-medium, 'file': 'HelveticaNeue-Medium'),
    ('name': $helvetica-neue-ff,  'style': normal, 'weight': $fw-regular, 'file': 'HelveticaNeue-Regular'),
    ('name': $helvetica-neue-ff,  'style': normal, 'weight': $fw-light, 'file': 'HelveticaNeue-Light'),
);

@each $font in $fonts {
    @font-face {
        font-family: map-get($font, 'name');
        font-display: swap;
        font-style: map-get($font, 'style');
        font-weight: map-get($font, 'weight');
        src: url('../fonts/#{map-get($font, 'file')}.woff2') format('woff2');
    }
}
