/*
    general layout styles
    put .container here or other global layout classes

    example:

    .container {
        max-width: $containerMaxWidth;
        margin-left: auto;
        margin-right: auto;
    }

*/

*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    position: relative;
    overflow-x: hidden;
    font-size: 100%;

    &.is-blocked {
        overflow: hidden;
    }

    &.is-blocked-touch {
        position: fixed;
        overflow-y: scroll;
        width: 100%;
        height: auto;
    }
}

body {
    margin: 0;
    color: var(--color-main-dark);
    background-color: var(--theme-color-bg);
    font-family: $ff-financier-text;
    font-weight: $fw-regular;
    line-height: 1.55;
    -webkit-font-smoothing: antialiased;
}

figure {
    margin: 0;
}

blockquote {

    @include tablet {
    }

    @include desktop-wide {
    }

    @include tablet {
    }
}

.visually-hidden {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    clip-path: inset(100%);
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    white-space: nowrap;
}

.devider {
    margin-bottom: 30px;
}

.main {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    margin-top: var(--top-menu-height);

    @include tablet {
        margin-right: var(--sidebar-width);
        margin-top: 0;
    }
}

.container {
    @include container;

    &--hero {
        height: 100%;
    }

    &--text-media {
        position: relative;
        width: 100%;
    }

    &--texts-slider {
        width: 100%;

        @include desktop {
            padding-right: col(1);
            padding-left: col(1);
        }

        @include desktop-medium {
            padding-right: col(2);
            padding-left: col(2);
        }
    }
}

img {
    max-width: 100%;
    height: auto;
}

.hidden-content {
    display: none;
}

[data-whatintent='touch'] *:focus {
    outline: none;
}
